* {
  box-sizing: border-box;
}

body {
  background-color: #919af9;
}

.loader {
  color: #000;
  margin: 20% 50%;
  font-family: Roboto, sans-serif;
  font-size: 40px;
  font-weight: 400;
  display: inline-block;
  position: relative;
  transform: translateX(-50%);
}

.loader:after {
  content: "Please wait";
  z-index: 5;
  position: relative;
}

.loader:before {
  content: "";
  height: 6px;
  width: 100%;
  background: linear-gradient(#515ddd 100%, rgba(0, 0, 0, 0) 0) 0 0 / 0% no-repeat;
  border: 1px solid;
  border-radius: 10px;
  animation: londingLine 10s linear infinite;
  position: absolute;
  bottom: 0;
}

@keyframes londingLine {
  to {
    background-size: 100%;
  }
}

.cat-info {
  column-gap: 20px;
  display: flex;
}

.description {
  max-width: 50%;
  font-size: 20px;
  font-weight: 500;
}

